<template>
    <div class="panel" :style="size">
        <router-view />
    </div>
    
</template>


<script>
export default {
    name: 'FloatingPanel',
    props:{
        width: {
            type: Number,
            default: 300
        },
        height: {
            type: Number,
            default: 300
        }
    },

    computed:{
        size(){
            return `width: ${this.width}px; height: ${this.height}px; `;
        }
    }
}
</script>


<style scoped>
.panel {
    background-color: aliceblue;
    
    
}
</style>