import bbox from 'geojson-bbox';
import consts from "./consts";

export default {

    getBounds(features){
        const b = bbox(features);
        const bounds = [{lat:b[1],lng:b[0]},{lat:b[3],lng:b[2]}];
        return bounds;
    },
    
    getYear(feature) {
        const yearNotFound = consts.labelOtherYears;
        const date = this.getDate(feature);
        if(!date) return yearNotFound;
        const year = date.getFullYear();
        return isNaN(year)? yearNotFound : year;
    },
    getDate(feature){
        const licDate = feature.properties.lic_date;
        if(!licDate) return null;
        return new Date(licDate);
    },
    getType(feature){ return feature.properties.lic_type; },
    getCode(feature) { return feature.properties.pro_code; },

    getLabelAndData(map){
        const labels = Object.keys(map);
        const data = Object.values(map);
        return {labels, data};
    },
    featureSelected(selectedFeatures, feature){
        const gjson = this.filterGeoJsonByProperty(selectedFeatures, 'pro_code', this.getCode(feature));
        return gjson.features.length > 0;
    },

    // COUNT BY
    countValuesInMap(map){
        const newMap = new Map();
        for (const key in map) {
            if (Object.hasOwnProperty.call(map, key)) {
                const elements = map[key];
                newMap[key] = elements.length;
            }
        }
        return newMap;
    },
    countFeaturesByTypes(features){
        const types = this.groupFeaturesByTypes(features);
        return this.countValuesInMap(types);
    },
    countFeaturesByYears(features){
        const years = this.groupFeaturesByYears(features);
        return this.countValuesInMap(years);
    },
    countFeaturesByProvinces(features){
        const provinces = this.groupFeaturesByProvinces(features);
        return this.countValuesInMap(provinces);
    },

    // GROUP BY
    groupFeaturesBy(xs, key) {
        return xs.reduce((rv, x) => {
          (rv[x.properties[key]] = rv[x.properties[key]] || []).push(x);
          return rv;
        }, {});
    },
    groupFeaturesByTypes(features){
        return this.groupFeaturesBy(features, 'lic_type');
    },
    groupFeaturesByYears(features){
        const years = new Map();
        for (const f in features) {
            if (Object.hasOwnProperty.call(features, f)) {
                const feature = features[f];
                const year = this.getYear(feature);
                const current = years[year];
                years[year] = current? [...current, feature] : [feature];
            }
        }
        return years;
    },
    groupFeaturesByProvinces(features){
        const provinces = new Map();
        for (let i = 0; i < features.length; i++) {
            const f = features[i];
            if(!f.properties || !f.properties.loc_khet) continue;
            const ps = f.properties.loc_khet.split(',');
            for (let j = 0; j < ps.length; j++) {
                const p = ps[j].trim();
                const current = provinces[p];
                provinces[p] = current? [...current, f]: [f];
            }
        }        
        return provinces;
    },

    // FILTER 
    filterGeoJson(geojson, filter){
        const features = geojson.features.filter(f => filter(f));
        return {...geojson, features: features};
    },
    filterGeoJsonByProperty(geojson, propertyName, value, filter){
        if(!filter) {return this.filterGeoJson(geojson, (f) => {return f.properties[propertyName] === value});}
        return this.filterGeoJson(geojson, (f) => {return f.properties[propertyName] === value && filter(f)});
    }
}